/* Error Page  */
.container-404 {
  margin: auto;
  width: 50%;
  padding: 10px;
  font-size: 20px;
  font-family: sans-serif; }

.h3 {
  position-fixed: center; }

/* Code Editor */
.CodeEditor {
  position: relative; }

.CodeEditor-options {
  margin: 15px;
  text-align: center;
  font-size: 20px; }
  .CodeEditor-options .select-option {
    display: block;
    width: 100px;
    margin: 0 auto; }
    .CodeEditor-options .select-option option {
      width: inherit; }
  .CodeEditor-options button {
    width: 150px;
    height: 60px;
    font-size: 20px; }

.CodeEditor-options select {
  padding: 3px;
  margin: 5px; }

/* Tag Input */
.TagInput-label {
  display: block;
  margin: 0 auto;
  margin-top: 20px; }

/* ReactTags Styling */
/* STYLES FOR THE MAIN DIV */
div.tagsClass {
  position: relative;
  margin: 0 auto;
  border-radius: 10px;
  height: 100px; }

/* STYLES FOR INPUT */
div.tagInputClass input.tagInputFieldClass {
  width: inherit;
  margin: 5px;
  font-size: 20px;
  border-radius: 10px;
  text-indent: 10px; }

/* STYLES FOR SELECTED TAGS AND REMOVE BUTTON */
div.selectedClass span.tagClass {
  position: relative;
  border-radius: 10px;
  background: #008B8B;
  color: #FFFFFF;
  font-size: 20px;
  display: inline-block;
  padding: 10px;
  margin: 5px;
  cursor: move; }

div.selectedClass a.removeClass {
  position: relative;
  padding-left: 10px;
  color: #000000;
  font-size: 20px;
  height: 50%;
  margin: 0 auto; }

div.selectedClass a.removeClass:hover {
  color: green;
  cursor: pointer; }

/* STYLES FOR SUGGESTIONS */
div.suggestionsClass {
  position: absolute;
  border: 1px solid black;
  border-radius: 2px;
  margin: 10px 5px;
  background-color: pink; }

div.suggestionsClass ul {
  list-style-type: none;
  display: table-row;
  background: white;
  -webkit-box-shadow: 0.05em 0.01em 0.5em rgba(0, 0, 0, 0.2);
  box-shadow: 0.05em 0.01em 0.5em rgba(0, 0, 0, 0.2); }

div.suggestionsClass li {
  padding: 5px 10px;
  margin: 0; }

div.suggestionsClass li mark {
  text-decoration: underline;
  background: none;
  font-weight: 600; }

div.suggestionsClass ul li.activeSuggestionClass {
  background: #b7cfe0;
  cursor: pointer; }

/* ERROR */
.error-msg {
